import React from 'react'

const Field = () => {
  return (
    <div>
    <div className ='bg'>



       <style jsx>{`
       .bg {
       background: url(/static/images/pitch2.png) center scroll;
       background-repeat: no-repeat;
       height: 400px;
         }`}
      </style>
    </div>
    </div>
  )
}

export default Field



